body{
  background-color: #444;
  padding: 20px;
  margin: 0;
}
h1,h2,p,ul,li{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

ul.header li{
  display: inline;
  list-style-type: none;
  margin: 1rem;
  font-size: 20px;
}

ul.header{
  background-color: black;
  padding: 0;
}
ul.header li a{
  color: white;
  font-weight: bold;
  text-decoration: none;
  padding: 15px;
  display: inline-block;
}

ul.header li a:hover{
  color: #6a6e71;
  text-decoration: none;
}
.content{
  background-color: transparent;
  padding: 20px;
  height: auto;
  color: #ced4da;
}
.content{
  padding: 0;
  margin: 0;
}
.content li{
  margin-bottom: 10px;
}

.logo{
  color: #f8f5f5;
  font-size: 1.5rem;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 18px 0;
}
.logo:hover{
  color: #080808;
  text-decoration: none;
}
.home h2{
    color: #eece1a;
    font-size: 40px;
    margin-bottom: 0.6rem !important;
}

.home p{
    color: #ced4da;
    font-size: 20px;
}

.home{
    font-weight: bolder;
    font-size: larger;
    padding: 1.5rem;
}
.border{
    border-bottom: 1px solid;
    margin: 1rem 3rem 1rem 3rem;
}
.about{
    text-align: center;
    font-size: 20px;
}
